import { z } from 'zod';

export const AddLetterSchema = z.object({
    letter_for: z.string().min(1, 'This field is required'),
    note: z.string().min(1, 'This field is required'),
    attachment: z.instanceof(File, { message: 'File is required' }),
});

export type AddLetterFormValues = z.infer<typeof AddLetterSchema>;
