import { Dialog, DialogContent, DialogHeader } from '@/components/ui/dialog';
import AddLetterForm from '@/features/selfServices/forms/services/AddLetter';
import { cn } from '@/lib/utils';

interface Props {
    open: boolean;
    setAddLetter: (value: boolean) => void;
    handleRefreshTable: () => void;
}

export default function AddLetterRequest({ open, setAddLetter,handleRefreshTable }: Props) {
    return (
        <Dialog open={open}>
            <div
                className={cn('fixed inset-0 bg-black/50', {
                    '!hidden': !open,
                })}
            />
            <DialogContent
                showCloseButton={false}
                className="max-w-[623px] rounded-lg p-8 bg-white-blue-50 font-cairo gap-0 [&_[aria-label=Close]]:opacity-0"
            >
                <DialogHeader className="flex flex-col items-center">
                    <div className="flex flex-row justify-between mb-4 w-full">
                        <div className="flex flex-row gap-2">
                            <svg
                                width="25"
                                height="25"
                                viewBox="0 0 25 25"
                                fill="none"
                                xmlns="http://www.w3.org/2000/svg"
                            >
                                <path
                                    opacity="0.4"
                                    d="M21.5 7.5V17.5C21.5 20.5 20 22.5 16.5 22.5H8.5C5 22.5 3.5 20.5 3.5 17.5V7.5C3.5 4.5 5 2.5 8.5 2.5H16.5C20 2.5 21.5 4.5 21.5 7.5Z"
                                    fill="#125592"
                                />
                                <path
                                    d="M19 9.75H17C15.48 9.75 14.25 8.52 14.25 7V5C14.25 4.59 14.59 4.25 15 4.25C15.41 4.25 15.75 4.59 15.75 5V7C15.75 7.69 16.31 8.25 17 8.25H19C19.41 8.25 19.75 8.59 19.75 9C19.75 9.41 19.41 9.75 19 9.75Z"
                                    fill="#145DA0"
                                />
                                <path
                                    d="M12.5 14.25H8.5C8.09 14.25 7.75 13.91 7.75 13.5C7.75 13.09 8.09 12.75 8.5 12.75H12.5C12.91 12.75 13.25 13.09 13.25 13.5C13.25 13.91 12.91 14.25 12.5 14.25Z"
                                    fill="#145DA0"
                                />
                                <path
                                    d="M16.5 18.25H8.5C8.09 18.25 7.75 17.91 7.75 17.5C7.75 17.09 8.09 16.75 8.5 16.75H16.5C16.91 16.75 17.25 17.09 17.25 17.5C17.25 17.91 16.91 18.25 16.5 18.25Z"
                                    fill="#145DA0"
                                />
                            </svg>

                            <h3 className="text-[22px] font-bold text-primery-blue-500">طلب اصدار خطاب</h3>
                        </div>
                        {/* <SubtleButton className="bg-primery-blue-50 text-base font-medium leading-[125%] text-primery-blue-500 border border-primery-blue-50">
                            <DocumentUpload />
                            لرفع مستند
                        </SubtleButton> */}
                    </div>
                    <div className="w-full border-b border-[#D1D1D1] !mt-0"></div>
                </DialogHeader>
                <AddLetterForm setAddLetter={setAddLetter} handleRefreshTable={handleRefreshTable}/>
            </DialogContent>
        </Dialog>
    );
}
